body {
  margin: 0;
  padding: 0;
  font-family:  'Roboto',sans-serif;
}
h2{
  font-size: 20px;
  font-weight: 400;
  color:#1e1e24;  
}
h3{
  font-size: 18px;
  font-weight: 400;
  color:#1e1e24;  
}
h4{
  font-size: 16px;
  font-weight: 500;
  margin: 0;
  color:#1e1e24;  
}
p{
  font-size: 16px;  
  font-weight: 400;
  color:#1e1e24;
}
a{
  font-weight: 500;
}
button{
  font-family:  'Roboto',sans-serif;
  font-size: 14px;
  font-weight: 500;
  letter-spacing: 1px;
  text-decoration: none;
  border-radius: 5px;
  border:0;
  cursor: pointer;
}
input, textarea{
  border: 1px solid #779cab;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 400;
  border-radius: 5px;  
}
input::-moz-placeholder, textarea::-moz-placeholder { /* Firefox 19+ */
  color: #779cab;
  font-size: 14px;
}
input:-moz-placeholder, textarea:-moz-placeholder { /* Firefox 18- */
  color: #779cab;
  font-size: 14px;
}
label{
  color:#1e1e24;  
}


@media screen and (max-width: 1080px) {
  h2{
    font-size: 18px;
    font-weight: 400;
    color:#1e1e24;  
  }
  h3{
    font-size: 16px;
    font-weight: 400;
    color:#1e1e24;  
  }
  h4{
    font-size: 14px;
    font-weight: 500;
    margin: 0;
    color:#1e1e24;  
  }
  p{
    font-size: 14px;  
    font-weight: 400;
    color:#1e1e24;
  }
  a{
    font-weight: 500;
  }
}
@media screen and (max-width: 720px) {
  h2{
    font-size: 16px;
    font-weight: 400;
    color:#1e1e24;  
  }
  h3{
    font-size: 14px;
    font-weight: 400;
    color:#1e1e24;  
  }
  h4{
    font-size: 12px;
    font-weight: 500;
    margin: 0;
    color:#1e1e24;  
  }
  p{
    font-size: 12px;  
    font-weight: 400;
    color:#1e1e24;
  }
  a{
    font-weight: 500;
  }

}



